/* General container styling */

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f0f2f5;
}


/* Login form styling */

.login-form {
    background-color: #fff;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
    text-align: center;
}

.login-form h2 {
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
}


/* Input field styling */

.input-group {
    margin-bottom: 20px;
    text-align: left;
}

.input-group label {
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
    color: #555;
}

.input-group input {
    width: 100%;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    font-size: 16px;
}

.input-group input:focus {
    border-color: #007bff;
    outline: none;
}


/* Login button styling */

.login-button {
    width: 70%;
    padding: 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.login-button:hover {
    background-color: #0056b3;
}


/* Register prompt styling */

.register-prompt {
    margin-top: 20px;
}

.register-prompt button {
    background: none;
    border: none;
    color: #0008ff;
    cursor: pointer;
    font-size: 16px;
    text-decoration: underline;
}

.register-prompt button:hover {
    color: #0207a0;
    background-color: #ffffff;
}